

















export default {
	data() {
		return {
			news : []
		}
	},
	onLoad:function() {
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.news = res.data;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openInfoPage(e) {
			console.log(e);
			var newsid = e.currentTarget.dataset.newsid;
			console.log(newsid);
			// 打开页面
			uni.navigateTo({
				url: '../info/info?newsid='+newsid
			});
		}
	}
}
